// hooks/useUniswapPool.ts
var REFETCH_INTERVAL = 6e4;
var useUniswapPool = (chainId, poolAddress) => {
  const [pool, setPool] = useState(null);
  const [liquidity, setLiquidity] = useState("0");
  const { data, isError, isLoading } = useReadContracts({
    contracts: [
      { address: poolAddress, abi: IUniswapV3PoolABI.abi, functionName: "token0", chainId },
      { address: poolAddress, abi: IUniswapV3PoolABI.abi, functionName: "token1", chainId },
      { address: poolAddress, abi: IUniswapV3PoolABI.abi, functionName: "fee", chainId },
      { address: poolAddress, abi: IUniswapV3PoolABI.abi, functionName: "liquidity", chainId },
      { address: poolAddress, abi: IUniswapV3PoolABI.abi, functionName: "slot0", chainId }
    ]
  });
  const {
    data: token0Balance,
    refetch: refetchTokenBalance,
    isRefetching: isRefetchingUniswap
  } = useReadContract2({
    address: data?.[0].result,
    abi: erc20abi_default,
    functionName: "balanceOf",
    args: [poolAddress],
    chainId,
    query: {
      enabled: !!data?.[0]?.result,
      refetchInterval: REFETCH_INTERVAL
    }
  });
  useEffect2(() => {
    if (data && token0Balance !== void 0) {
      const token0Address = data[0].result;
      const token1Address = data[1].result;
      const fee = data[2].result;
      const uniswapLiquidity = data[3].result;
      const slot0 = data[4].result;
      if (token0Address && token1Address) {
        const [sqrtPriceX96, tick] = slot0;
        const token0 = new Token(
          chainId,
          token0Address,
          18,
          "Ggas",
          "Ggas"
        );
        const token1 = new Token(
          chainId,
          token1Address,
          18,
          "wstETH",
          "wstETH"
        );
        const poolInstance = new Pool(
          token0,
          token1,
          fee,
          sqrtPriceX96.toString(),
          uniswapLiquidity.toString(),
          tick
        );
        setPool(poolInstance);
        const formattedToken0Balance = (Number(token0Balance) / 10 ** token0.decimals).toFixed(4);
        setLiquidity(formattedToken0Balance);
      }
    }
  }, [data, token0Balance, chainId]);
  const refetchUniswapData = useCallback(() => {
    if (!isRefetchingUniswap) {
      refetchTokenBalance();
    }
  }, [isRefetchingUniswap, refetchTokenBalance]);
  return { pool, liquidity, isError, isLoading, refetchUniswapData };
};

